type Translations = {
  english: string;
  ukrainian: string;
  polish: string;
  albanian: string;
};


// NEW: Updated Card type to include an optional audio source path.
type Card = {
  german: string;
  translations: Translations;
  audioSrc?: string;
};

type LanguageKey = keyof Translations;

type Blank = { answer: string; size: number };
type SpeakerLabel = { speaker: string };
type LearningContentPart = string | Blank | SpeakerLabel;

type LearningContent = {
    id: number;
    title: string;
    level: string;
    type: 'passage' | 'dialogue';
    fullText: string;
    parts: LearningContentPart[];
    audioSrc: string;
    vocabulary: Card[];
};

type PassagePart = string | { answer: string; size: number };

type Passage = {
    id: number;
    title: string;
    level: string;
    fullText: string;
    parts: PassagePart[];
    audioSrc: string;
};

