        fullText: `Friseurin: Hallo! Haben Sie einen Termin? Kundin: Ja, ich bin Emma Richter. Ich habe um 14 Uhr einen Termin. Friseurin: Ah ja, Frau Richter! Ich bin Sabine. Was mchten Sie denn heute machen lassen? Kundin: Ich mchte meine Haare schneiden lassen. Nicht zu kurz, aber ein bisschen krzer. Friseurin: Verstehe. Etwa 5 Zentimeter krzer? Und mchten Sie auch eine neue Farbe? Kundin: Nein, die Farbe gefllt mir. Nur schneiden, bitte. Friseurin: In Ordnung. Kommen Sie mit, ich wasche Ihnen erst die Haare. Kundin: Wie lange dauert das ungefhr? Friseurin: Mit Waschen und Schneiden etwa 45 Minuten. Kundin: Das passt gut. Ich habe um 16 Uhr noch einen anderen Termin. Friseurin: Das schaffen wir problemlos. Setzen Sie sich bitte hier hin.`,
